.header { padding: 12px 0; background: #ffffff; position: relative; z-index: 10; }

@media only screen and (max-width: 1040px) { .header { padding: 35px 0; position: fixed; left: 0; top: 0; width: 100%; box-shadow: 5px 5px 15px 0 rgba(31, 31, 31, 0.07); } }

.header .menu-open { position: absolute; left: 20px; top: 50%; transform: translate(0, -50%); width: 30px; height: 30px; display: none; z-index: 99; }

@media only screen and (max-width: 1040px) { .header .menu-open { display: flex; } }

.header .menu-open span { width: 30px; height: 3px; margin-top: 13px; background: #72787d; display: block; border-radius: 2px; }

.header .menu-open span:before, .header .menu-open span:after { content: ''; width: 30px; height: 3px; background: #72787d; position: absolute; left: 0; border-radius: 2px; }

.header .menu-open span:before { top: 3px; }

.header .menu-open span:after { bottom: 4px; }

.header__content { display: flex; align-items: center; justify-content: space-between; }

@media only screen and (max-width: 1040px) { .header__logo { margin-left: 60px; display: flex; justify-content: center; align-items: center; } }

.header__nav { margin-right: 200px; }

@media only screen and (max-width: 1040px) { .header__nav { display: none; } }

.header__nav__list { display: flex; align-items: center; }

.header__nav__list a { font-size: 16px; font-weight: normal; color: #3c4646; padding-bottom: 7px; }

.header__nav__list li { margin-right: 17px; position: relative; }

.header__nav__list li i { font-size: 10px; position: relative; top: 1px; transition: all 0.3s; margin-left: 4px; }

.header__nav__list li:hover i, .header__nav__list li:hover a { color: #ff191b; }

.header__nav__list li:hover .header__nav__dropdown { visibility: visible; opacity: 1; }

.header__nav__list li:hover .header__nav__dropdown a { color: #3c4646; }

.header__nav__list li.active a { border-bottom: 1px solid #ff191b; }

.header__nav__dropdown { visibility: hidden; opacity: 0; transition: all 0.3s; position: absolute; left: 0; top: 30px; background-color: #f9f9f9; padding: 15px 10px 5px; display: flex; flex-direction: column; flex-wrap: wrap; width: 620px; max-height: 290px; }

.header__nav__dropdown li { margin-right: 25px; }

.header__nav__dropdown a { margin-bottom: 7px; display: block; width: 180px; }

.header__nav__dropdown a:hover { color: #ff191b !important; }

.header__nav__dropdown-secondary { width: 125px; height: auto; }

.header__connection { position: relative; display: flex; }

.header__connection a { color: #5a6464; font-size: 18px; margin-right: 20px; }

@media only screen and (max-width: 767px) { .header__connection a { font-size: 22px; margin-right: 30px; } }

.header__connection a:hover { color: #ff191b; }

.header__connection__search { width: 20px; overflow: hidden; }

.header__connection__search.is-active a { display: block; }

.header__connection__search.is-active button { left: 0; }

.header__connection__search div { width: 0px; position: absolute; background: #ffffff; right: 0; top: -10px; padding: 10px 0; border-bottom: 1px solid #dadada; display: flex; justify-content: center; align-items: center; transition: all 0.3s; }

.header__connection__search div.is-active { width: 340px; }

@media only screen and (max-width: 1100px) { .header__connection__search div.is-active { width: 300px; } }

@media only screen and (max-width: 767px) { .header__connection__search div.is-active { width: calc(100vw - 90px); } }

.header__connection__search div input { width: 100%; font-weight: normal; color: #373d3d; margin-left: 18px; border-bottom: none; padding: 0; }

.header__connection__search div input::placeholder { color: #aeb8b8; }

.header__connection__search div button { background: none; display: block; position: relative; transition: all 0.3s; }

.header__connection__search div button:hover i { color: #ff191b; }

.header__connection__search div button i { font-size: 18px; text-align: center; font-weight: 900; color: #5a6464; transition: all 0.3s; }

@media only screen and (max-width: 767px) { .header__connection__search div button i { font-size: 22px; } }

.header__connection__search div a { color: #aeb8b8; display: none; margin-right: 0; }

.header__connection__search div a i { font-size: 13px; }

@media only screen and (max-width: 767px) { .header__connection__search div a i { font-size: 15px; } }

body { font-family: 'Montserrat', sans-serif; box-sizing: border-box; position: relative; font-size: 16px; line-height: 1.38; color: #373d3d; }

@media only screen and (max-width: 767px) { body { font-size: 20px; } }

.container { width: 100% !important; max-width: 1135px; box-sizing: border-box; }

@media only screen and (max-width: 1150px) { .container { padding: 0 15px; } }

a { text-decoration: none; transition: all 0.3s; }

input[type="submit"] { appearance: none; -webkit-appearance: none; background: none; }

textarea { resize: none; }

h2 { font-size: 30px; font-weight: 600; text-align: center; color: #5a6464; text-transform: uppercase; }

h3 { font-size: 19px; text-transform: uppercase; font-weight: 600; color: #5a6464; }

input, textarea { border: none; box-shadow: none; border-bottom: 1px solid #dadada; padding: 15px; font-size: 16px; color: #373d3d; }

input::placeholder, textarea::placeholder { color: #aeb8b8; }

@media only screen and (max-width: 767px) { input, textarea { font-size: 20px; } }

input:focus, textarea:focus { border-color: #5a6464; }

input:invalid, textarea:invalid { color: #ff191b; border-color: #ff191b; }

input[type="number"]::-webkit-outer-spin-button, input[type="number"]::-webkit-inner-spin-button { display: none; -webkit-appearance: none; margin: 0; }

@media only screen and (max-width: 767px) { .hidden-xs-1 { display: none; } }

@media only screen and (max-width: 767px) { .hidden-xs { display: none !important; } }

@media only screen and (min-width: 768px) and (max-width: 1040px) { .hidden-sm { display: none !important; } }

@media only screen and (min-width: 1041px) { .hidden-md { display: none !important; } }

.hidden { display: none; }

.swiper-button-prev, .swiper-button-next { background: none; width: 58px; height: 58px; background-color: #5a6464; display: flex; justify-content: center; align-items: center; z-index: 70; }

@media only screen and (max-width: 767px) { .swiper-button-prev, .swiper-button-next { display: none; } }

.swiper-button-prev i, .swiper-button-next i { color: #ffffff; font-size: 35px; position: relative; }

.swiper-button-prev { left: 0; }

.swiper-button-prev i { left: -3px; }

.swiper-button-next { right: 0; }

.swiper-button-next i { right: -3px; }

.swiper-button-disabled { opacity: 1 !important; }

.swiper-pagination-bullet { width: 14px; height: 14px; background-color: #aeb8b8; opacity: 1; margin: 0 13px !important; }

.swiper-pagination-bullet-active { background: #5a6464; }

.swiper-pagination { bottom: 40px !important; }

.btn { display: flex; justify-content: center; align-items: center; display: inline-flex; padding: 8px 26px; border: solid 1px #ff191b; font-size: 16px; font-weight: 500; line-height: 1; text-align: center; color: #ff2c2e; transition: all 0.3s; }

@media only screen and (max-width: 767px) { .btn { font-size: 20px; } }

.btn:hover { background: #ff191b; color: #ffffff; }

.breadcrumbs { display: flex; align-items: center; position: relative; z-index: 2; flex-wrap: wrap; }

.breadcrumbs a { font-size: 14px; font-weight: normal; color: #f43524; position: relative; margin-bottom: 5px; }

@media only screen and (max-width: 767px) { .breadcrumbs a { font-size: 18px; } }

.breadcrumbs a:hover:before { opacity: 1; }

